import Vue from 'vue';
import Router from 'vue-router';
import Hello from '@/components/Hello';
import Movie from '@/components/movie/Page';
import Person from '@/components/Person';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello,
    },
    {
      path: '/movie/:movie_id',
      name: 'Movie',
      component: Movie,
    },
    {
      path: '/person/:person_id',
      name: 'Person',
      component: Person,
    },
  ],
});



