<template>
    <article class="movie">
      <header class="banner">
        <div class="banner__image" :style="'backgroundImage:' + `url('https://image.tmdb.org/t/p/w1280/${movie.backdrop_path}')`" />
        <div class="overlay" />
      </header>
      <section>
          <h1 class="text-center">{{movie.title}}</h1>
          <h5 class="text-center">{{movie.tagline}}</h5>
          <ul class="inline center">
            <li class="tag" v-for="genre in movie.genres">{{genre.name}}</li>
          </ul>
          <p style="max-width: 620px; margin-bottom: 2rem" class="center">{{movie.overview}}</p>
        </div>
        <!-- <li v-for="video in movie.videos.results">
          <iframe width="560" height="315" 
          :src="`https://www.youtube.com/embed/${video.key}`" 
          frameborder="0" 
          allow="autoplay; encrypted-media" 
          allowfullscreen>
          </iframe>
        </li> -->
          <h1 class="text-center">Cast</h1>
          <table class="center">
            <tr v-for="item in movie.credits.cast"  @click="$router.push({name:'Person', params: {person_id: item.id }})">
              <td><img height="64" :src="`https://image.tmdb.org/t/p/w1280/${item.profile_path}`" alt=""></td>
              <td>{{item.character}} </td>
              <td>{{item.name}}</td>
            </tr>
          </table>
          <!-- Budget: {{movie.budget}}
          Revenue: {{movie.revenue}} -->
          <!-- <ul>
            <li v-for="lang in movie.spoken_languages">{{lang.name}}</li>
          </ul> -->
          <!-- <p>Release Date {{movie.release_date}}</p>
          <p>Runtime {{movie.runtime}}</p>
          <p>{{movie.homepage}}</p> -->
          <!-- <li v-for="item in movie.credits.crew">
            {{item.job}}: {{item.name}}
          </li> -->
      </section>
    </article>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Movie',
  data() {
    return {
      movie: [],
    };
  },
  created() {
    this.getMovie();
  },
  methods: {
    getMovie() {
      const movieID = this.$route.params.movie_id;
      axios.get(`https://api.themoviedb.org/3/movie/${movieID}?api_key=a99cb79115ec65cdb37247ef52887471&append_to_response=credits,recommendations`)
        .then((res) => { this.movie = res.data; });
    },
  },
};
</script>

<style scoped lang="scss">
.banner {
  position: fixed;
  height: 70vh;
  width: 100%;
  z-index: 0;
  overflow: hidden;
  .banner__image {
    width: 100%;
    height: 100%;
    .overlay {
      position: absolute;
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      background-color: rgba(0,0,0,0.5);
      z-index: 0;
    }
  }
}

.center {
  margin:  0 auto;
  justify-content: center;
  text-align: justify;
}

.text-center {
  text-align: center;
}

.inline {
  display: flex;
  list-style: none;
  padding: 0;
}

.tag {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  font-size: 0.9rem;
  border-radius: 0.5rem;
  background-color: #F9690E;
  margin: 0.25rem
}

section {
  position: relative;
  top: 60vh;
  z-index: 1;
  max-width: 80%;
  margin: 0 auto;
  background-color: #222;
  padding: 2rem;
  box-shadow: 0 0 6px 1px rgba(0,0,0,0.45);
}

table { 
    border-spacing: 0;
    border-collapse: separate;
}
tr td {
  cursor: pointer;
  border: 1px solid #333;
  text-align: center;
  img {
    display: block;
  }
}
</style>


