<template>  
  <div class="grid">
    <router-link v-for="(result,index) in movies" :key="index" :to="`movie/${result.id}`">
      <movie-poster :movie="result" />
    </router-link>
    <div ref="sentinel" class="sentinel" />
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'hello',
  data() {
    return {
      observer: null,
      currentPage: 1,
      currentMedia: 'movie',
      movies: [],
    };
  },
  mounted() {
    this.getMedia(this.currentMedia, 1);
    this.observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          this.currentPage = this.currentPage + 1;
          this.getMedia(this.currentMedia, this.currentPage);
        }
      }, { rootMargin: '120px' },
    );
    this.observer.observe(this.$refs.sentinel);
  },
  methods: {
    clearMovies() {
      this.movies = [];
    },
    changeMedia(type) {
      this.clearMovies();
      this.currentMedia = type;
    },
    getMedia(type, page) {
      axios.get(`https://api.themoviedb.org/3/discover/${type}?page=${page}&api_key=a99cb79115ec65cdb37247ef52887471`)
        .then((res) => { this.movies = this.movies.concat(res.data.results); });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
* {
  box-sizing: border-box;
}
.grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 0.25rem;
  align-items: center;
  justify-content: center;
  padding-top: 4.5rem;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



