<template>
    <article class="person">
      <section>
          <img width="150" :src="`https://image.tmdb.org/t/p/w1280/${movie.profile_path}`" />
          <h1 class="text-center">{{movie.name}}</h1>
          <h5 class="text-center">{{movie.place_of_birth}}</h5>
          <p style="max-width: 620px; margin-bottom: 2rem" class="center">{{movie.biography}}</p>
      </section>
    </article>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Movie',
  data() {
    return {
      movie: [],
    };
  },
  created() {
    this.getMovie();
  },
  methods: {
    getMovie() {
      const movieID = this.$route.params.person_id;
      axios.get(`https://api.themoviedb.org/3/person/${movieID}?api_key=a99cb79115ec65cdb37247ef52887471&append_to_response=movie_credits`)
        .then((res) => { this.movie = res.data; });
    },
  },
};
</script>

<style scoped lang="scss">
.banner {
  position: fixed;
  height: 70vh;
  width: 100%;
  z-index: 0;
  overflow: hidden;
  .banner__image {
    width: 100%;
    height: 100%;
    .overlay {
      position: absolute;
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      background-color: rgba(0,0,0,0.5);
      z-index: 0;
    }
  }
}

.center {
  margin:  0 auto;
  justify-content: center;
  text-align: justify;
}

.text-center {
  text-align: center;
}

.inline {
  display: flex;
  list-style: none;
  padding: 0;
}

.tag {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  font-size: 0.9rem;
  border-radius: 0.5rem;
  background-color: #F9690E;
  margin: 0.25rem
}

section {
  position: relative;
  top: 60vh;
  z-index: 1;
  max-width: 80%;
  margin: 0 auto;
  background-color: #222;
  padding: 2rem;
  box-shadow: 0 0 6px 1px rgba(0,0,0,0.45);
}

table { 
    border-spacing: 0;
    border-collapse: separate;
}
tr td {
  cursor: pointer;
  border: 1px solid #333;
  text-align: center;
  img {
    display: block;
  }
}
</style>


