<template>
    <div class="movie-poster">
      <progressive-image :src="`https://image.tmdb.org/t/p/w300${movie.poster_path}`" />
    </div>
</template>

<script>
export default {
  name: 'movie-poster',
  props: ['movie'],
};
</script>

<style scoped lang="scss">
.movie-poster {
  text-align: center;
  cursor: pointer;
  border: 1px solid transparent;
}
</style>


